import { Component, OnDestroy, OnInit } from "@angular/core";
import { Subscription } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { StateService } from '@services/state.service';
import { UserManager } from '@services/app/managers/user.manager';
import { App } from '@services/app/app.api';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {
    public active: string;
    public get user(): App.User { return UserManager.sharedInstance.user; }

    private subscriptions: Array<Subscription> = [];

    constructor(private route: ActivatedRoute, public router: Router, private state: StateService) { }

    public ngOnInit() {
    }

    public ngOnDestroy() {
        this.subscriptions.forEach(o => o.unsubscribe());
    }

    public onLogOut() {
        this.state.logout();
    }
}
